<template>
  <div id="app">
    <h1 class="title"> 女神小工具 </h1>
    <div class="columns">
      <div class="column">
        <div class="field">
          <div class="control">
            普通抽轉換成鑽石:
            <input class="input" type="number" placeholder="drawConvertToDiam" v-model.number="drawConvertToDiam" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      <div class="column">
        <div class="field">
          <div class="control">
            神器抽轉換成鑽石:
            <input class="input" type="number" placeholder="draw2ConvertToDiam" v-model.number="draw2ConvertToDiam" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      
    </div>
    <div class="columns">
      <div class="column has-text-left">
        <p>普通抽轉換成鑽石: {{ drawc2Diam() }}</p>
        <p>神器抽轉換成鑽石: {{ draw2c2Diam() }}</p>     
      </div>  
    </div>
    <div class="columns">
      <div class="column">
        <div class="field">
          <div class="control">
            金牌:
            <input class="input" type="number" placeholder="card" v-model.number="card" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      <div class="column">
        <div class="field">
          <div class="control">
            神器牌:
            <input class="input" type="number" placeholder="card2" v-model.number="card2" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      
    </div>
    
    <div class="columns">
      <div class="column">
        <div class="field">
          <div class="control">
            鑽石(總數):
            <input class="input" type="number" placeholder="totdiamond" v-model.number="totdiamond" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      <div class="column">
        <div class="field">
          <div class="control">
            鑽石(分配普通抽):
            <input class="input" type="number" placeholder="diamond" v-model.number="diamond" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      <div class="column">
        <div class="field">
          <div class="control">
            鑽石(分配神器抽):
            <input class="input" type="number" placeholder="diamond2" v-model.number="diamond2" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>            
      
    </div>
    <div class="columns">
      <div class="column">
        <div class="field">
          <div class="control">
            選擇包(分配普通抽):
            <input class="input" type="number" placeholder="selectPack1" v-model.number="selectPack1" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
      <div class="column">
        <div class="field">
          <div class="control">
            選擇包(分配神器抽):
            <input class="input" type="number" placeholder="selectPack2" v-model.number="selectPack2" @change="checkNegative" min="0" />
          </div>
        </div>
      </div>
    </div>

    <div class="columns">
      <div class="column has-text-left">
        <p>普通抽總數: {{ totalDraws() }}</p>
        <p>神器抽總數: {{ totalDraws2() }}</p>
        <p>分配後剩餘鑽石: {{ restOfDiam() }}</p>
        <!--p>Total: ${{ result || 0 }}</p-->
        <!--p>Total: {{ total() }}</p-->  
       
      </div>  
    </div>
    <div class="columns">
      <div class="column">
        <button class="button is-info is-rounded is-fullwidth" @click="reset">
          <span class="icon">
            <i class="fas fa-undo"></i>
          </span>
          <span>Reset</span>
        </button>  
      </div>  
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
      return {
        card: '0',
        diamond: '0',
        diamond2: '0',
        card2: '0',
        selectPack1: '0',
        selectPack2: '0',
        totdiamond: '0',
        drawConvertToDiam: '0',
        draw2ConvertToDiam: '0',
      }
    },
    methods: {
      drawc2Diam() {
        var d2diam = (parseInt(this.drawConvertToDiam) * 88 )
        return d2diam
      },
      draw2c2Diam() {
        var d2diam = (parseInt(this.draw2ConvertToDiam) * 66 )
        return d2diam
      },
      totalDraws() {
        var totCard = (parseInt(this.card) * 1)
        var totdiaToDraw = (parseInt(this.diamond) / 88)
        var totselectPackToCard = (parseInt(this.selectPack1) * 3)
        
        var total = totCard + totdiaToDraw + totselectPackToCard
        return Math.round(total)
      },
      totalDraws2() {
        var totdiaToDraw = (parseInt(this.diamond2) / 66)
        var totcard2 = (parseInt(this.card2))
        var totselectPackToCard2 = (parseInt(this.selectPack2) * 4)
        var total = totdiaToDraw + totcard2 + totselectPackToCard2
        return Math.round(total)
      },
      total(){
        return this.totalDraws()
      },
      restOfDiam() {
        var totdiam = parseInt(this.totdiamond)
        var totDiaDraw = parseInt(this.diamond)
        var totDiaDraw2 = parseInt(this.diamond2)
        var restOfDiam = totdiam - totDiaDraw - totDiaDraw2
        return Math.round(restOfDiam)
      },
      reset() {
        this.card = '0';
        this.diamond = '0';
        this.totdiamond = '0';
        this.diamond2 = '0';
        this.card2 = '0';
        this.selectPack1 = '0';
        this.selectPack2 = '0';
        this.drawConvertToDiam = '0';
        this.draw2ConvertToDiam = '0';
     
      },
      checkNegative() {
        if (this.card < 0){
          this.card = 0;
        }
        if (this.totdiamond < 0){
           this.totdiamond = 0;
        }
        if (this.diamond < 0){
          this.diamond = 0;
        }
        if (this.diamond2 < 0){
          this.diamond2 = 0;
        }
        if (this.card2 < 0 ){
          this.card2 = 0;
        }
       if  (this.selectPack1 < 0){
          this.selectPack1 = 0;
       }
       if  (this.selectPack2 < 0){
          this.selectPack2 = 0;
       }
      }
    }
}
</script>

